@import url(https://fonts.googleapis.com/css?family=Lato|Raleway|Roboto&display=swap);
html {
  position: relative;
  min-height: 100%; }

body {
  font-family: 'Raleway', 'Lato', 'Roboto', sans-serif;
  padding: 0;
  text-align: justify; }

::-webkit-scrollbar {
  width: 10px; }

::-webkit-scrollbar-track {
  background-color: #FFF;
  border-radius: 50px; }

::-webkit-scrollbar-thumb {
  background-color: #CBCBCB;
  border-radius: 50px; }

::-webkit-scrollbar-thumb:hover {
  background-color: #909090; }

button:focus,
button:active {
  outline: none !important;
  box-shadow: none !important; }

.navbar-dark {
  padding-top: 2rem;
  padding-bottom: 2rem; }

.social-links {
  display: grid;
  grid-template-columns: repeat(12, 1fr); }

@media (min-width: 576px) {
  .social-links {
    display: inherit; } }

.footer {
  background-color: black;
  padding: 2rem 0;
  width: 100%;
  /* Set the fixed height of the footer here */ }
  .footer .title {
    color: #fff; }
  .footer .description {
    color: #fff;
    margin-bottom: 0; }
  .footer .footer-nav-item {
    text-align: center;
    border-right: 1px solid rgba(255, 255, 255, 0.5); }
  .footer .footer-nav-item:last-child {
    border: none; }
  .footer .footerlink {
    color: #ddd; }
    .footer .footerlink:hover {
      color: #fff;
      text-decoration: none; }
  .footer .footer-social {
    font-size: 2rem;
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 2rem; }
    .footer .footer-social .footer-social-item {
      display: block;
      text-align: center; }

.section {
  padding: 6rem 0;
  font-size: 1.2rem;
  text-align: left;
  height: 100vh; }
  @media (min-width: 575px) {
    .section {
      text-align: justify;
      height: auto; } }

.dark {
  background: black;
  color: white; }

@media (max-width: 575px) {
  .portfolio-image {
    height: 0 !important; } }

.landing {
  position: relative;
  background: url(/Sai-Charan-Kommana/static/media/hero.4d0c761f.jpg) no-repeat center;
  background-size: cover;
  height: 100vh; }
  .landing-inner {
    color: white;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: auto;
    justify-content: center; }
  .landing h1 {
    letter-spacing: 1rem;
    text-transform: uppercase; }
  .landing .lead {
    font-size: 1.5rem; }
  .landing .dark-overlay {
    height: 100%;
    width: 100%;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.75)), to(rgba(0, 0, 0, 0.25)));
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.25));
    background: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.25)); }
  .landing .arrow-nav {
    display: none; }
  @media (min-width: 575px) {
    .landing .arrow-nav {
      position: absolute;
      display: block;
      top: 90%;
      left: 50vw;
      -webkit-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
      text-align: center; }
      .landing .arrow-nav-text {
        font-size: 1.3rem; }
      .landing .arrow-nav .arrow-btn {
        background: none;
        border: none; }
        .landing .arrow-nav .arrow-btn .down-arrow {
          background: url(/Sai-Charan-Kommana/static/media/down-arrow.ee66ba05.png) center no-repeat;
          background-size: cover;
          height: 30px;
          width: 30px; }
        .landing .arrow-nav .arrow-btn:focus {
          outline: 0; } }

.quote {
  font-size: 1.8rem;
  padding-bottom: 6rem; }
  .quote .heading {
    padding: 2rem 0; }

.contact-container {
  height: 100vh;
  width: 40%;
  margin-top: 75px;
  margin-bottom: 75px;
  padding: 75px 0; }
  .contact-container .form-group {
    margin-top: 75px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly; }

.card {
  margin-right: 10px;
  margin-bottom: 30px;
  display: block;
  border-radius: 15px;
  min-height: 30rem;
  width: 500px; }
  .card a, .card a:hover {
    color: black;
    text-decoration: none; }
  .card-image {
    height: 15rem;
    background-size: contain;
    background-repeat: no-repeat; }

.gamedev-container {
  margin-top: 75px;
  margin-bottom: 75px; }

.game-portfolio {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: stretch; }

